import infoBox from "../../components/boxstyle/infobox.vue"
import tabBar from "../../components/tabbar2.vue"
import "../../../common/itemcontent.css"
import "../../../common/twolinelayout.css"
import tipImg from "../carwarn.vue"

export default {
	data() {
		return {
			tabList: ["已申请", "待出厂", "运输中", "已完成"],
			// 选中状态
			current: 0,
			// 控制页面显示detail 
			showDetail: false,
			// 保存一份选中详情页面的当前current
			lastStatus: 0,
			// 控制提示消息页面
			showTipsList: [false, false, false, false],
			// 1-4页面的数据
			applyList: null,
			goOutList: [],
			transitList: [],
			doneList: [],
			userInfo: null,
			// 获取货物详情列表
			goodsList: [],
			// 当前货单的id
			id: 0,
			// 已完成列表的详情
			doneListDetail: [],
			modifyArr: [],
			// 页面中长字符串的截取
			strTwo: '..',
			strThree: '..',
			strFour: [],
			strThreeCurrent: '..',
			detailCurrent: '',
			doneUserInfo: '',
			// 物料列表
			goodsDetail: [],
			detailStr: []
		}
	},

	methods: {
		getCurrent(data) {
			this.current = data;
			// 点击 tab 栏，只要和保存的current发生变化就返回列表页面。
			if (this.lastStatus !== data) {
				this.showDetail = false;
			}
			this.getOrderList();
		},
		// 待出场点击部分
		async firstClick() {
			const opts = {
				url: '/personal/driver/getDetails?orderId=' + this.id,
				method: 'post'
			};

			const res = await this.$http.httpTokenRequest(opts);
			this.userInfo = res.data.result;
			this.goodsList = res.data.result.materielDetails;

			this.strThreeCurrent = this.userInfo.logisticsLocation.slice(0, 6)

			console.log(res);
			console.log(this.userInfo)

			this.showDetail = true;
			// 保存状态
			this.lastStatus = this.current;
		},

		// 获取完成订单
		async doneClick(i) {
			const opts = {
				url: '/personal/driver/getOrderRecords',
				method: 'post'
			};
			const res = await this.$http.httpTokenRequest(opts);

			this.doneListDetail = res.data.result;

			this.detailCurrent = i;

			this.doneUserInfo = this.doneListDetail[this.detailCurrent];

			// 详情显示的字符串
			this.detailStr = this.strFour[i]

			console.log(this.doneUserInfo)
			this.modifyArr = [];

			this.doneListDetail.map((i, idx) => {
				this.modifyArr.push(JSON.parse(i.materiel))
			})

			this.goodsDetail = this.modifyArr[i]

			console.log(this.modifyArr);
			// 控制页面状态
			this.showDetail = true;
			// 保存状态
			this.lastStatus = this.current;
		},

		// 获取表单id值
		async getOrderId() {
			const opts = {
				url: '/personal/driver/getOrderRecord',
				method: 'post'
			};
			const res = await this.$http.httpTokenRequest(opts);
			console.log(res)
			this.id = res.data.result.id
		},

		async getOrderList() {
			const opts = {
				url: '/personal/driver/getTaskAppRecordList',
				method: 'post'
			};

			const currentStatus = this.current === 3 ? 5 : this.current;

			if (currentStatus == 0) {
				this.getApplyList();
			} else {
				console.log("currentStatus:" + currentStatus)

				const params = {
					current: 0,
					data: {
						orderStatus: currentStatus
					}
				}
				const res = await this.$http.httpTokenRequest(opts, params);
				// 分别判断
				if (this.current === 1) {this.goOutList = res.data.result.records}
				if (this.current === 2) {this.transitList = res.data.result.records}
				if (this.current === 3) {this.doneList = res.data.result.records}
				
				console.log(res.data.result.records.length === 0);
				
				if (res.data.result.records.length === 0) {
					this.$set(this.showTipsList, this.current, true)
					// this.showTipsList[this.current] = true
				}
				console.log(this.current)
				console.log(this.showTipsList);

				// 获取当前地址截取字符
				if (this.current === 1) {
					console.log(this.goOutList)
					this.strTwo = this.goOutList[0].destination.slice(0, 6)
					console.log(strTwo)
				}
				if (this.current === 2) {
					this.strThree = this.transitList[0].destination.slice(0, 6)
				}
				if (this.current === 3) {
					this.doneList.map((item, idx) => {
						console.log(this.doneList)
						// this.$set(this.strFour, idx, item.destination.slice(0, 6))
						console.log(this.strFour);
						// this.strFour = [];
						this.strFour.push(item.destination.slice(0, 6))
					})
				}

				console.log(res);
			}

		},

		// 获得已申请页面的数据
		async getApplyList() {
			const opts = {
				url: '/personal/driver/getTaskAppRecord',
				method: 'post'
			};
			const res = await this.$http.httpTokenRequest(opts);
			console.log(res);
			this.applyList = res.data.result;
			if (res.data.result.carNo == null) {
				this.showTipsList[this.current] = true;
			}
		},
		// 获取当前的身份状态
		statusOfMan() {
			const status = uni.getStorageSync("USER_STATUS");
			console.log(status);
			if (status === "CONSIGNEE") {
				uni.reLaunch({
					url: "/pages/user/user"
				})
				uni.showToast({
					title: "请切换至其它身份",
					icon: "none"
				})
			}
		},

		showWay(i) {
			uni.showModal({
				content: i
			})
		}
	},
	onShow() {
		// this.getApplyList();
		this.getOrderList();
		this.getOrderId();
	},
	onHide() {
		uni.redirectTo({
			url: "/pages/home/home"
		})
	},
	onLoad() {
		this.statusOfMan();
		this.getOrderId();
		this.getApplyList();
	},
	components: {
		tabBar,
		infoBox,
		tipImg
	}


}
